<template>
  <div>
    <BottomSheet
      btnColor="child06"
      btnName=""
      btnClass="font-weight-bold mt-3"
      sheetHeight="260px"
      customBtnWidth="120px"
      :closeSheet="closeSheet"
      @sheetClosed="closeSheet = false"
    >
      <template v-slot:body>
        <v-card class="rounded-0" :elevation="0">
          <v-toolbar color="child04" dark :elevation="0" class="black--text"></v-toolbar>
          <div class="mx-4">
            <div class="mt-6">
              <h3>   ?</h3>
              <v-row class="mt-1">
                <v-col cols="8">
                  <v-text-field
                    v-model="depositAmount"
                    label="  ."
                    required
                    color="child04"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="4" align-self="center">
                  <h3 class="text-center"></h3>
                </v-col>
              </v-row>
            </div>
            <v-btn
              block
              color="child04"
              class="font-weight-bold"
              @click="[addSavingHistory(), $emit('updateSavingHistory')]"
              ></v-btn
            >
          </div>
        </v-card>
      </template>
    </BottomSheet>
    <!--  -->
    <v-snackbar app v-model="snackbar.isOpen" :timeout="snackbar.timeout" color="child05">
      <span class="black--text font-weight-bold">{{ snackbar.text }}}</span>
      <template v-slot:action="{ attrs }">
        <v-btn
          color="red"
          text
          v-bind="attrs"
          @click="snackbar.isOpen = false"
          class="font-weight-bold"
        >
          
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import BottomSheet from "@/components/common/BottomSheet.vue";
import { addAccountWithdraw } from "../../../api/accountsAPI";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "SavingHistoryBtmSheet",
  components: {
    BottomSheet,
  },
  data() {
    return {
      depositAmount: null,
      isWithDrawDone: false,
      closeSheet: false,
      snackbar: {
        isOpen: false,
        text: " .",
        timeout: 2000,
      },
    };
  },
  computed: {
    ...mapGetters("userStore", ["checkUserInfo"]),
  },
  methods: {
    ...mapActions("savingStore", [
      "vuexPostAddSavingHistory",
      "vuexGetSavingDetails",
      "vuexGetSavingBalance",
    ]),
    //  
    async addSavingHistory() {
      //   
      const formData = {
        category: "",
        childSeq: this.checkUserInfo.seq,
        content: "",
        money: this.depositAmount,
      };
      //  
      await addAccountWithdraw(
        formData,
        async () => {
          // console.log(" ");
          const formData = {
            childSeq: this.checkUserInfo.seq,
            transferAmount: this.depositAmount,
          };
          await this.vuexPostAddSavingHistory(formData);
          await this.vuexGetSavingBalance(this.checkUserInfo.seq);
          await this.vuexGetSavingDetails(this.checkUserInfo.seq);
          // alert("   .");
          this.snackbar.text = "   .";
          this.snackbar.isOpen = true;
        },
        (err) => {
          console.log(err);
          this.snackbar.text = " .";
          this.snackbar.text = "   .";
          this.snackbar.isOpen = true;
        }
      );
      this.closeSheet = true;
      this.depositAmount = null;
    },
  },
};
</script>
