<template>
  <div class="mx-9 mt-10">
    <h1>{{ isParent ? "" : "" }} </h1>
    <v-form v-model="valid">
      <v-text-field
        v-model="signupInfo.name"
        :rules="checkValid.name"
        label=""
        required
        :color="isParent ? 'parent01' : 'child01'"
      ></v-text-field>
      <v-text-field
        v-model="signupInfo.nickname"
        :rules="checkValid.nickname"
        label=""
        required
        :color="isParent ? 'parent01' : 'child01'"
      ></v-text-field>
      <v-text-field
        v-model="signupInfo.phone"
        :rules="checkValid.phone"
        label=""
        required
        :color="isParent ? 'parent01' : 'child01'"
      ></v-text-field>
      <v-text-field
        v-model="signupInfo.email"
        :rules="checkValid.email"
        label=""
        required
        :color="isParent ? 'parent01' : 'child01'"
      ></v-text-field>
      <v-text-field
        v-model="signupInfo.password"
        :rules="checkValid.password"
        label=""
        required
        :color="isParent ? 'parent01' : 'child01'"
        type="password"
      ></v-text-field>
      <v-text-field
        v-model="passwordConfirm"
        :rules="checkValid.passwordConfirm"
        label=" "
        required
        :color="isParent ? 'parent01' : 'child01'"
        type="password"
      ></v-text-field>
      <v-btn
        :disabled="!valid"
        block
        :color="isParent ? 'parent01' : 'child01'"
        class="font-weight-bold mt-4"
        :class="{ 'white--text': isParent }"
        @click.native="isParent ? parentSignup() : childSignup()"
        > </v-btn
      >
    </v-form>
  </div>
</template>

<script>
import { apiSignup } from "@/api/userAPI.js";

export default {
  name: "Signup",
  props: {
    isParent: Boolean,
  },
  data() {
    return {
      valid: true,
      checkValid: {
        email: [(v) => !!v || " ."],
        name: [(v) => !!v || " ."],
        nickname: [(v) => !!v || " ."],
        password: [(v) => !!v || " ."],
        passwordConfirm: [
          (v) => !!v || "    .",
          (v) => v === this.signupInfo.password || "  .",
        ],
        phone: [(v) => !!v || " ."],
      },
      signupInfo: {
        email: null,
        name: null,
        nickname: null,
        password: null,
        phone: null,
      },
      passwordConfirm: null,
    };
  },
  methods: {
    parentSignup() {
      apiSignup(this.isParent, this.signupInfo)
        .then(() => {
          // console.log(res);
          this.$router.push({ name: "Login" });
        })
        .catch(
          // (err) => {
          //   console.log(err);
          // }
          () => {}
        );
    },
    childSignup() {
      apiSignup(this.isParent, this.signupInfo)
        .then(() => {
          // console.log(res);
          this.$router.push({ name: "Login" });
        })
        .catch(
          // (err) => {
          //   console.log(err);
          // }
          () => {}
        );
    },
  },
};
</script>

<style></style>
