<template>
  <div>
    <BottomSheet
      :btnColor="`child01`"
      :btnName="isBuy ? '' : ''"
      btnClass="font-weight-bold mt-3"
      sheetHeight="280px"
      :isBlockBtn="true"
      :closeSheet="closeSheet"
      @sheetClosed="closeSheet = false"
    >
      <template v-slot:body>
        <v-card class="rounded-0" :elevation="0">
          <v-toolbar
            :color="isBuy ? 'child01' : 'child04'"
            dark
            :elevation="0"
            class="black--text"
            >{{ isBuy ? " " : " " }}</v-toolbar
          >
          <div class="mx-4">
            <div v-if="isBuy" class="mt-4">
              <h3>{{ name }} ?</h3>
              <p>   .</p>
              <v-row>
                <v-col cols="6">
                  <v-text-field
                    v-model="investAmount"
                    label="  "
                    required
                    :color="`child01`"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="2" align-self="center">
                  <v-icon>mdi-arrow-right</v-icon>
                </v-col>
                <v-col cols="4" align-self="center">
                  <h3 class="text-center">{{ totalPrice }}</h3>
                </v-col>
              </v-row>
            </div>

            <div v-else class="mt-4">
              <h3>{{ name }}  ?</h3>
              <p>   .</p>
              <v-row>
                <v-col cols="6">
                  <v-text-field
                    v-model="investAmount"
                    label="  "
                    required
                    :color="`child04`"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="2" align-self="center">
                  <v-icon>mdi-arrow-right</v-icon>
                </v-col>
                <v-col cols="4" align-self="center">
                  <h3 class="text-center">{{ totalPrice }}</h3>
                </v-col>
              </v-row>
            </div>

            <v-btn
              block
              :color="isBuy ? 'child01' : 'child04'"
              class="font-weight-bold"
              @click="requestExchange()"
              >{{ isBuy ? "" : "" }}</v-btn
            >
          </div>
        </v-card>
      </template>
    </BottomSheet>
    <!--  -->
    <v-snackbar app v-model="snackbar.isOpen" :timeout="snackbar.timeout" color="child02">
      <span class="black--text font-weight-bold">{{ snackbar.text }}</span>
      <template v-slot:action="{ attrs }">
        <v-btn
          color="red"
          text
          v-bind="attrs"
          @click="snackbar.isOpen = false"
          class="font-weight-bold"
        >
          
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import BottomSheet from "@/components/common/BottomSheet.vue";
import { apiPutInvestStatus } from "@/api/investAPI";

export default {
  components: {
    BottomSheet,
  },
  computed: {
    totalPrice: function () {
      return this.investAmount * this.price;
    },
  },
  methods: {
    async requestExchange() {
      let info = {
        count: this.isBuy ? this.investAmount : -this.investAmount,
        price: this.price,
        seq: this.investStatusSeq,
      };
      // console.log(info);
      await apiPutInvestStatus(
        info,
        () => {
          this.$emit("getData", this.investStatusSeq);
        },
        () => {
          // (error) => {
          // console.log(error);
          // alert(
          //   this.isBuy ? " !" : "      !"
          // );
          this.snackbar.text = this.isBuy
            ? " !"
            : "      !";
          this.snackbar.isOpen = true;
        }
      );
      this.closeSheet = true;
      this.snackbar.text = "  ";
      this.snackbar.isOpen = true;
    },
  },
  data() {
    return {
      investAmount: 0,
      closeSheet: false,
      snackbar: {
        isOpen: false,
        text: null,
        timeout: 2000,
      },
    };
  },
  props: {
    isBuy: {
      type: Boolean,
      default: true,
    },
    price: {
      type: Number,
    },
    name: {
      type: String,
    },
    investStatusSeq: {
      type: Number,
    },
  },
};
</script>

