<template>
  <div>
    <v-card class="rounded-0" :elevation="0">
      <v-toolbar color="parent01" class="white--text" :elevation="0">  </v-toolbar>
      <div class="pa-3">
        <v-subheader>   .</v-subheader>
        <v-form class="mx-4">
          <v-text-field label="" suffix="" color="parent01" v-model="money"></v-text-field>
        </v-form>
        <v-card-actions>
          <v-btn
            block
            color="parent01"
            @click="[addAccount(), $emit('clickDeposit')]"
            class="white--text"
          >
            
          </v-btn>
        </v-card-actions>
      </div>
    </v-card>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "Deposit",
  props: {
    childSeq: Number,
  },
  data() {
    return {
      money: null,
    };
  },
  methods: {
    ...mapActions({ accountStore: ["vuexAddAccountHistoryDeposit"] }),
    addAccount() {
      this.$store.dispatch("accountStore/vuexAddAccountHistoryDeposit", {
        category: "",
        childSeq: this.childSeq,
        content: " ",
        money: this.money,
      });
      this.money = null;
    },
  },
};
</script>

