  author: 
  modified :  -    vuex 
-->

<template>
  <div>
    <div class="mx-9 mt-10">
      <!-- /  -->
      <v-tabs :color="color">
        <v-tab @click="loginTypeParent"> </v-tab>
        <v-tab @click="loginTypeChild"> </v-tab>
      </v-tabs>

      <!--  -->
      <h1 class="mt-6"></h1>

      <!--   -->
      <v-form v-model="valid">
        <!--  input -->
        <v-text-field
          v-model="credentials.nickname"
          :rules="nicknameRules"
          label=""
          required
          :color="color"
        ></v-text-field>

        <!--  input -->
        <v-text-field
          v-model="credentials.password"
          :rules="passwordRules"
          label=""
          required
          :color="color"
          type="password"
          @keyup.enter="login"
        ></v-text-field>

        <!--  btn -->
        <v-btn
          :disabled="!valid"
          :color="color"
          block
          :class="{ 'white--text': isParent }"
          class="font-weight-bold mt-4"
          @click="login"
        >
          
        </v-btn>
        <!--  btn -->
        <v-btn
          :to="isParent ? '/parent/signup' : '/child/signup'"
          :color="color"
          outlined
          block
          class="font-weight-bold mt-2"
        >
          {{ isParent ? " " : " " }}
        </v-btn>
      </v-form>
    </div>
    <!--  -->
    <v-snackbar
      app
      v-model="snackbar.isOpen"
      :timeout="snackbar.timeout"
      :color="isParent ? 'parent02' : 'child02'"
      class="pb-10"
    >
      <span class="black--text font-weight-bold">    .</span>
      <template v-slot:action="{ attrs }">
        <v-btn
          color="red"
          text
          v-bind="attrs"
          @click="snackbar.isOpen = false"
          class="font-weight-bold"
        >
          
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { mapGetters, mapState, mapActions } from "vuex";

export default {
  name: "Login",
  data() {
    return {
      valid: true,
      isParent: true,
      color: "parent01",
      credentials: {
        nickname: null,
        password: null,
      },
      nicknameRules: [(v) => !!v || " ."],
      passwordRules: [(v) => !!v || " ."],
      snackbar: {
        isOpen: false,
        text: null,
        timeout: 2000,
      },
    };
  },
  computed: {
    ...mapGetters("userStore", ["checkUserInfo", "checkIsLogin"]),
    ...mapState("userStore", ["isLogin", "isLoginError"]),
  },
  methods: {
    ...mapActions("userStore", ["vuexLogin", "vuexGetUserInfo"]),

    ...mapActions("parentStore", ["vuexGetChildren", "vuexGetTutorialStage"]),

    async login() {
      const loginInfo = {
        isParent: this.isParent,
        credentials: this.credentials,
      };

      // vuex jwt 
      await this.vuexLogin(loginInfo);
      // vuex   
      await this.vuexGetUserInfo(loginInfo);

      //   vuex   
      if (this.checkIsLogin) {
        if (this.isParent) {
          await this.vuexGetChildren(this.checkUserInfo.seq);
          await this.vuexGetTutorialStage(this.checkUserInfo.seq);
        }
        this.$router.push(this.isParent ? "/parent/home" : "/child/home");
      } else {
        this.snackbar.isOpen = true;
      }
    },
    loginTypeParent() {
      this.isParent = true;
      this.color = "parent01";
    },
    loginTypeChild() {
      this.isParent = false;
      this.color = "child01";
    },
  },
};
</script>

<style></style>
